@import url(https://fonts.googleapis.com/css?family=Roboto&display=swap);
html {
  height: 100%;
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  font-family: 'Montserrat', sans-serif;
  position: relative;
  height: 100%;
  margin: 0;
  padding: 0;
}

.App {
  font-family: sans-serif;
  text-align: center;
}

link {
  text-decoration: none;
  font-display: none;
  font-family: Arial, Helvetica, sans-serif;
}

* {
  padding: 0px;
  margin: 0px;
  outline: 0;
  box-sizing: border-box;
}

body,
html {
  height: 100%;
  font: 480 14px roboto, sans-serif;
  background-image: url(/portfolio/static/media/img6.c774d900.jpg);
  background-position: center;
}

